<template>
  <div class="timer-container">
    <h1>üíç –î–æ —Å–≤–∞–¥—å–±—ã –æ—Å—Ç–∞–ª–æ—Å—å ‚è≥</h1>
    <p class="countdown">{{ formatTime }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      weddingDate: new Date("2025-06-22T00:00:00"),
      now: new Date(),
    };
  },
  computed: {
    formatTime() {
      const diff = this.weddingDate - this.now;

      if (diff <= 0) return "üéâ –°–æ–±—ã—Ç–∏–µ —É–∂–µ –Ω–∞—Å—Ç—É–ø–∏–ª–æ!";

      const days = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((diff % (1000 * 60)) / 1000);

      return `${days} –¥–Ω–µ–π ${hours} —á–∞—Å–æ–≤ ${minutes} –º–∏–Ω—É—Ç ${seconds} —Å–µ–∫—É–Ω–¥`;
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      this.now = new Date();
    }, 1000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  },
};
</script>

